
.page-search {
  position: relative;
  height: 100%;
}
.page-search .vux-tab-container {
  height: 1.22667rem;
}
.page-search .vux-tab-container .vux-tab {
  height: 1.22667rem;
}
.page-search .vux-tab-container .vux-tab .vux-tab-item {
  line-height: 1.22667rem;
  font-size: 0.37333rem;
}
.page-search__content {
  top: 2.56rem;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  overflow-y: auto;
}
.page-search__content .weui-cells {
  margin-top: 0;
}
.page-search__item {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  box-sizing: border-box;
  height: 2.17333rem;
  padding: 0.64rem 0.66667rem !important;
}
.page-search__item-pic {
  width: 2.24rem;
  font-size: 0;
}
.page-search__item-pic img {
  height: 1.28rem;
  width: 100%;
}
.page-search__item-content {
  padding-left: 0.48rem;
}
.page-search__item-content h5 {
  font-size: 0.42667rem;
  padding-bottom: 0.26667rem;
  font-weight: normal;
}
.page-search__item-content p {
  font-size: 0.34667rem;
  color: #999;
}



