
body {
  margin: 0;
}
.page-login {
  background-color: #fff;
  height: 100%;
}
.page-login__logo {
  height: 5.33333rem;
  background: url("~@/assets/logo.png") no-repeat center center;
  background-size: 3.33333rem;
}
.page-login__form {
  padding: 0 0.90667rem;
}
.page-login__button {
  height: 1.2rem;
  margin-top: 0.8rem;
}
.page-login__input {
  text-align: center;
  border-bottom: 0.02667rem solid #ddd;
  margin-bottom: 0.26667rem;
  margin-top: 0.26667rem;
}
.page-login__input input {
  font-size: 0.42667rem;
  width: 100%;
  border: none;
  height: 0.85333rem;
}



