
.page-register__content {
  padding: 0 0.33333rem;
}
.page-register__content h5 {
  text-align: center;
  margin: 0;
  padding-top: 0.66667rem;
  padding-bottom: 0.46667rem;
  font-size: 0.50667rem;
  font-weight: 400;
}
.page-register__content p {
  text-align: center;
  margin: 0;
  padding-bottom: 0;
  font-size: 0.32rem;
  color: #666;
}
.page-register__content input {
  width: 100%;
  height: 1.2rem;
  background-color: #fcfcfc;
  border-radius: 0.13333rem;
  border: solid 0.02667rem #c8c8c8;
  font-size: 0.4rem;
  text-align: center;
  margin-top: 1.73333rem;
}
.page-register__content .weui-btn {
  height: 1.2rem;
  margin-top: 0.6rem;
  font-size: 0.45333rem;
}



