
.page-sign__content {
  padding: 0 0.33333rem;
  position: absolute;
  overflow-y: auto;
  top: 1.33333rem;
  bottom: 0;
  left: 0;
  right: 0;
}
.page-sign__content h5 {
  text-align: center;
  margin: 0;
  padding-top: 0.66667rem;
  padding-bottom: 0.46667rem;
  font-size: 0.50667rem;
  font-weight: 400;
}
.page-sign__content p {
  text-align: center;
  margin: 0;
  padding-bottom: 0;
  font-size: 0.32rem;
  color: #666;
}
.page-sign__content .weui-cell {
  height: 1.33333rem;
  font-size: 0.4rem;
}
.page-sign__content .weui-cell p {
  color: #000;
  font-size: 0.4rem;
}
.page-sign__content .weui-btn {
  height: 1.2rem;
  margin-top: 0.6rem;
  font-size: 0.45333rem;
  margin-bottom: 1.33333rem;
}
.page-sign__content a {
  text-decoration: none;
}



